import { Link } from "react-router-dom";
import health_providers_iso_10002 from "../../../../assets/images/content/health-providers-iso-10002.png";
import health_providers_iso_500 from "../../../../assets/images/content/health-providers-iso-500.jpg";
import health_providers_iso_9001_tuv from "../../../../assets/images/content/health-providers-iso-9001-tuv.png";
import jci_gold_seal from "../../../../assets/images/content/health-providers-jci-gold-seal.png";
import { providersList_en } from "./ProvidersList_en";
import { renderRating } from "common/helpers";

const HealthProvidersEn = ({ title }) => {
  return (
    <div className="terms-content">
      <h1> {title} </h1>
      <div className="terms-text">
        <p>
          Each medical service provider and facility in our network has been
          thoroughly reviewed to ensure that it meets our high standards and
          quality assurance tests for the safety and comfort of patients and
          their families.
        </p>
        <p>
          {" "}
          Prior to incorporating each hospital and clinic into our elite
          network, our team personally visited and inspected each location in
          order to ensure that it is accredited by the Turkish Ministry of
          Health and most are certified by the Joint Commission International
          (JCI) and/ or International Standards Organization (ISO).
        </p>
        <p>
          {" "}
          Medical services and procedures are performed by highly-qualified
          doctors and nurses with excellent bedside manners. Most of them have
          been schooled, trained, or practiced in North America or Europe and
          many are world renowned.
        </p>

        <p>
          We work closely with all medical service providers to ensure your
          needs are met and expectations are exceeded.
        </p>
        <p>
          We organize online interviews with the medical representatives, free
          of charge for patients before leaving their country, so that questions
          in the patient's mind can be answered, in addition to having the
          chance to get to know the doctor and get all the necessary information
          about the treatment from the source.
        </p>
      </div>
      {/* Provider Widget */}
      <div className="row">
        <div className="col-12">
          <div className="card">
            {providersList_en.map((provider) => (
              <div key={provider.id} className="card">
                <div className="card-body product-description">
                  <div className="doctor-widget">
                    <div className="doc-info-left">
                      <div className="doctor-img1 center-img">
                        <Link
                          to={`${provider.content_url}`}
                          state={{ providerData: provider }}
                        >
                          <img
                            src={provider.image}
                            className="img-fluid"
                            alt="User"
                          />
                        </Link>
                      </div>
                      <div className="doc-info-cont">
                        <h4 className="doc-name">
                          <Link
                            to={`${provider.content_url}`}
                            state={{ providerData: provider }}
                          >
                            {provider.provider_name}
                          </Link>
                        </h4>
                        <div className="rating">
                          {renderRating(provider.rating, "en")}
                          <span className="doc-speciality"> {` (${provider.rating} stars out of ${provider.total_rating} Google reviews)`}</span>
                        </div>
                        <p className="doc-speciality">{provider.brief}
                          <Link
                            className="intext-link"
                            to={`${provider.content_url}`}
                            state={{ providerData: provider }}
                          >
                            {` Read More`}
                          </Link>
                        </p>
                        <div className="doc-speciality">
                          {provider.certifications && <h5>Certifications</h5>}
                          <ul>
                            {provider.certifications?.map(c => <li>{c}</li>)}
                          </ul>
                        </div>
                        <div className="doc-speciality">
                          {provider.excellence_areas && <h5>Areas of Excellence</h5>}
                          <ul>
                            {provider.excellence_areas?.map(e => <li>{e}</li>)}
                          </ul>
                        </div>
                        {/* <h5 className="doc-department">
                          <img
                            src={provider.speciality.img}
                            className="img-fluid"
                            alt="Speciality"
                          />
                          {provider.speciality.name}
                        </h5> */}
                        {/* <div className="clinic-details">
                          <p className="doc-location">
                            <i className="fas fa-map-marker-alt"></i>{" "}
                            {provider.address}
                          </p>
                        </div> */}
                        {/* <div className="clinic-services">
                              <span>Dental Fillings</span>
                              <span> Whitneing</span>
                            </div> */}
                      </div>
                    </div>
                    <div className="doc-info-right">
                      <div className="clini-infos"></div>
                      <div className="clinic-booking">
                        <Link
                          className="view-pro-btn"
                          to={`${provider.content_url}`}
                          state={{ providerData: provider }}
                        >
                          View Details
                        </Link>
                        {/* <Link className="apt-btn" to="/patient/booking">
                      Book Appointment
                    </Link> */}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
        {/* /Provider Widget */}
        <div className="col-12">
          <div className="terms-text">
            <h4>Examples of accreditation:</h4>
          </div>
          <div className="terms-text mb-5">
            <div className="row align-items-center justify-content-center">
              <img
                className="mx-5 mt-3"
                width={"225px"}
                src={health_providers_iso_500}
              ></img>
              <img
                className="mx-5 mt-3"
                width={"150px"}
                src={health_providers_iso_9001_tuv}
              ></img>
              <img
                className="mx-5 mt-3"
                width={"150px"}
                src={health_providers_iso_10002}
              ></img>
              <img
                className="mx-5 mt-3"
                width={"150px"}
                src={jci_gold_seal}
              ></img>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default HealthProvidersEn;
