import MetaTags from "common/MetaTags";
import { Page } from "common/components";
import { useTranslation } from "react-i18next";
import { getDescription } from "../descriptions";
import HealthProvidersAr from "./health-providers-ar";
import HealthProvidersEn from "./health-providers-en";

const HealthProviders = ({ contentId, titleId }) => {
  const [t, i18n] = useTranslation();

  const getTitle = () => t(titleId);

  const metaTags = [
    {
      name: "description",
      content: getDescription(contentId, i18n.language),
    },
  ];

  const renderComponent = () => {
    if (i18n.language === "ar") {
      return <HealthProvidersAr title={getTitle()} />;
    } else {
      return <HealthProvidersEn title={getTitle()} />;
    }
  };

  return (
    <Page title={getTitle()}>
      <MetaTags title={getTitle()} tags={metaTags} />
      <div className="content">
        <div className="container">
          <div className="row">
            <div className="col-12">{renderComponent()}</div>
          </div>
        </div>
      </div>
    </Page>
  );
};

export default HealthProviders;
