import { Link } from "react-router-dom";
import health_providers_iso_10002 from "../../../../assets/images/content/health-providers-iso-10002.png";
import health_providers_iso_500 from "../../../../assets/images/content/health-providers-iso-500.jpg";
import health_providers_iso_9001_tuv from "../../../../assets/images/content/health-providers-iso-9001-tuv.png";
import jci_gold_seal from "../../../../assets/images/content/health-providers-jci-gold-seal.png";
import { providersList_ar } from "./ProvidersList_ar";
import { renderRating } from "common/helpers";


const HealthProvidersAr = ({ title }) => {
  return (
    <div className="terms-content">
      <div className="terms-text">
        <h1> {title} </h1>
        <p>
          تمت مراجعة كل مقدم خدمة طبية ومنشأة في شبكتنا بدقة للتأكد من كونها تفي
          بالمعايير العالية واختبارات ضمان الجودة لسلامة وراحة المرضى وعائلاتهم.
        </p>
        <p>
          قبل اعتمادنا أي مستشفى أو مرفق صحيّ أو فندق في شبكة مزوّدي الخدمات
          لدينا قام فريقنا شخصيًا بزيارة وتفتيش كل موقع للتأكد من أنه معتمد من
          قبل وزارة الصحة التركية وأن معظمهم معتمد من قبل اللجنة المشتركة
          الدولية (JCI) و/ أو المنظمة الدولية للمعايير (ISO)، وذلك لضمان سلامة
          وراحة المرضى وعائلاتهم. يتمّ تقديم الخدمات والإجراءات الطبية من قبل
          أطباء وممرضات مؤهلين تأهيلاً عالياً حتى فيما يخصّ سلوكيات التعامل مع
          المرضى، كما حصل معظمهم على تعليمٍ أوتدريبٍ أو خبرة ممارسةٍ في أمريكا
          الشمالية أو أوروبا، إضافةً إلى كونِ أسماء العديد منهم مشهورة عالميًا.
        </p>
        <p>
          تستخدمُ المرافق الصحية التي نعملُ معها أحدث التقنيات والمعدّات الطبية
          التي يديرها متخصصون معتمدون دوليًا وحاصلون على تدريبٍ محترف.
        </p>
        <p>
          فريقنا المهني معك بإذن الله في كل خطوة للتأكد من راحتك طوال رحلتك، حيث
          نعمل عن كثب مع جميع مقدمي الخدمات لضمان تلبية احتياجاتك وتجاوز
          التوقعات.
        </p>
        <p>
          نقوم بترتيب مقابلات عبر الإنترنت مع الطبيب مجانًا للمرضى قبل مغادرة
          بلدهم ، بحيث يمكن الإجابة على الأسئلة التي في لدى المريض ، بالإضافة
          إلى إتاحة الفرصة للتعرف على الطبيب والحصول على جميع المعلومات اللازمة
          حول العلاج من مصدر العلاج نفسه.
        </p>
      </div>
      {/* Provider Widget */}
      <div className="row">
        <div className="col-12">
          <div className="card">
            {providersList_ar.map((provider) => (
              <div key={provider.id} className="card">
                <div className="card-body product-description">
                  <div className="doctor-widget">
                    <div className="doc-info-left">
                      <div className="doctor-img1 center-img">
                        <Link
                          to={`${provider.content_url}`}
                          state={{ providerData: provider }}
                        >
                          <img
                            src={provider.image}
                            className="img-fluid"
                            alt="User"
                          />
                        </Link>
                      </div>
                      <div className="doc-info-cont">
                        <h4 className="doc-name">
                          <Link
                            to={`${provider.content_url}`}
                            state={{ providerData: provider }}
                          >
                            {provider.provider_name}
                          </Link>
                        </h4>
                        <div className="rating">
                          {renderRating(provider.rating, "ar")}
                          <span className="doc-speciality"> {` (${provider.rating} نجوم من ${provider.total_rating} مراجعات جوجل)`}</span>
                        </div>
                        <p className="doc-speciality">{provider.brief}
                          <Link
                            className="intext-link"
                            to={`${provider.content_url}`}
                            state={{ providerData: provider }}
                          >
                            {` المزيد`}
                          </Link>
                        </p>
                        <div className="doc-speciality">
                          {provider.certifications && <h5>الشهادات</h5>}
                          <ul>
                            {provider.certifications?.map(c => <li>{c}</li>)}
                          </ul>
                        </div>
                        <div className="doc-speciality">
                          {provider.excellence_areas && <h5>مجالات التميز</h5>}
                          <ul>
                            {provider.excellence_areas?.map(e => <li>{e}</li>)}
                          </ul>
                        </div>
                        {/* <h5 className="doc-department">
                          <img
                            src={provider.speciality.img}
                            className="img-fluid"
                            alt="Speciality"
                          />
                          {provider.speciality.name}
                        </h5> */}
                        {/* <div className="clinic-details">
                          <p className="doc-location">
                            <i className="fas fa-map-marker-alt"></i>{" "}
                            {provider.address}
                          </p>
                        </div> */}
                        {/* <div className="clinic-services">
                              <span>Dental Fillings</span>
                              <span> Whitneing</span>
                            </div> */}
                      </div>
                    </div>
                    <div className="doc-info-right">
                      <div className="clini-infos"></div>
                      <div className="clinic-booking">
                        <Link
                          className="view-pro-btn"
                          to={`${provider.content_url}`}
                          state={{ providerData: provider }}
                        >
                          التفاصيل
                        </Link>
                        {/* <Link className="apt-btn" to="/patient/booking">
                      Book Appointment
                    </Link> */}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
        {/* /Provider Widget */}
        <div className="col-12">
        <div className="terms-text">
          <h4>أمثلة على الاعتماد الدولي:</h4>
        </div>
          <div className="terms-text mb-5">
            <div className="row align-items-center justify-content-center">
              <img
                className="mx-5 mt-3"
                width={"225px"}
                src={health_providers_iso_500}
              ></img>
              <img
                className="mx-5 mt-3"
                width={"150px"}
                src={health_providers_iso_9001_tuv}
              ></img>
              <img
                className="mx-5 mt-3"
                width={"150px"}
                src={health_providers_iso_10002}
              ></img>
              <img
                className="mx-5 mt-3"
              width={"150px"}
              src={jci_gold_seal}
              ></img>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default HealthProvidersAr;
